body {
  color: #fff;
  background-color: #a459d1;
  padding: 2rem;
}

.quiz {
  padding: 2rem;
}

.quiz__question {
  text-align: center;
  font-weight: 900;
  font-size: 1.5rem;
}

.quiz__options {
  font-weight: 700;
  padding: 0.5rem;
}

.quiz__options > li {
  padding: 0.7rem 0;
  text-align: center;
  font-size: 1.3rem;
  border: 1px solid #ffffff;
  cursor: pointer;
  border-radius: 1.5rem;
}

.btn__next {
  float: right;
}

.correct {
  background-color: green;
}

.wrong {
  background-color: red;
}

.quiz__container {
  text-align: center;
}

.green {
  background-color: green;
}
